import React, { useState } from "react";
import { useTranslation } from 'react-i18next';
import {
  StarOutlined,
  ProductOutlined,
  ShopOutlined,
  ShoppingCartOutlined,
  VideoCameraAddOutlined,
  ToolOutlined,
  MailOutlined
} from "@ant-design/icons";
import type { MenuProps } from "antd";
import { Menu } from "antd";
import { useNavigate } from 'react-router-dom';

type MenuItem = Required<MenuProps>["items"][number];

const Mmenu: React.FC = () => {
    const { t } = useTranslation();
    const navigate = useNavigate();
  const items: MenuItem[] = [
  {
    label: t('menu.nav1'),
    key: "home",
    icon: <ShopOutlined />,
      onClick: () => navigate("/home")
  },
  {
    label: t('menu.nav2'),
    key: "SubMenu",
    icon: <ProductOutlined />,
    children: [
      {
        type: "group",
        children: [
          { label: t('Case Studies'), key: "setting:1",icon: <StarOutlined />,onClick: () => navigate("/casestudies")},
          { label: t('Webinars'), key: "setting:2" ,icon:<VideoCameraAddOutlined /> ,onClick: () => navigate("/webinars")},
          { label: t('Events'), key: "setting:3",icon:<MailOutlined /> ,onClick: () => navigate("/events")},
          { label: t('Help Center'), key: "setting:4" ,icon:<ToolOutlined />,onClick: () => navigate("/helpcenter")},
        ],
      },

    ],
  }, 

   {
    label: t('menu.nav3'),
    key: "price",
    icon: <ShoppingCartOutlined />,
  onClick: () => navigate("/price")
  },
];
  const [current, setCurrent] = useState("home");

  const onClick: MenuProps["onClick"] = (e) => {
    console.log("click ", e);
    setCurrent(e.key);
  };

  return (
    <Menu
      onClick={onClick}
      selectedKeys={[current]}
      mode="horizontal"
      items={items}
      style={{ fontWeight: "600",width: "100%" }}
    />
  );
};

export default Mmenu;
